import React from 'react';
import { Collapse, Col, Row } from 'antd';
import Container from 'components/UI/Container/Container';
import MemberWrapper from './Member.style';

const Member = () => {
  return (
    <MemberWrapper>      
      <Container fluid={true}>
        <Row gutter={30} style={{ marginTop: 30 }}>
            <Col xl={12}>
                <h1>MEMBERS	OF	THE	PREMIER	COMMUNITY</h1>
                <p>
                  The	Premier	Community	members	take	advantage	of;
                </p>
                <p>
                  1. Daily	earnings	sent	to	the	NFT Holders	wallet	while	staked.
                </p>
                <p>
                  2. Once	youve become	a	Premier	NFT	Holder	you	have	the	ability	to	
                  Level	up	to	the	next	asset	at	no	extra	cost	once	the	asset	(ie.	Cars, Real Estates, Planes and Boats) is upgraded.
                </p>
                <p>
                  3. The	architecture	of	this	community	creates	a	system	where	the	
                    business	and	the	Total	Asset	Value	continue	to	grow	over	time.	This	is	a	
                    cash	generating	business	brought	to	the	blockchain.	
                </p>
                <p>
                  4. The	NFTs	can	be	sold	on	the	secondary	market	on	Binance	NFT.
                </p>
                <p>
                  5. The NFT owner can lock the NFT and use the actual vehicle when on vacation to Orlando.
                </p>
            </Col>
            <Col xl={10} className="communityImagea">
                <img src="images/home_section2.png"></img>
            </Col>            
        </Row>
      </Container>    
    </MemberWrapper>
  );
};

export default Member;
