import React, { useContext } from 'react';
import { TopHotelsGrid, LuxaryHotelsGrid } from './Grid';
import SearchArea from './Search/Search';
import LocationGrid from './Location/Location';
import SampleGrid from './Sample/Sample';
import FAQ from './FAQ/FAQ';
import OurStory from './OurStory/OurStory';
import Roadmap from './Roadmap/Roadmap';
import InfoRoadmap from './InfoRoadmap/InfoRoadmap';
import WhatWeDo from './WhatWeDo/WhatWeDo';
import Member from './Member/Member';
import { LayoutContext } from 'context/LayoutProvider';
import { Waypoint } from 'react-waypoint';

const Home = () => {
  const [, dispatch] = useContext(LayoutContext);
  return (
    <>
      <SearchArea />
      <Waypoint
        onEnter={() => dispatch({ type: 'HIDE_TOP_SEARCHBAR' })}
        onLeave={() => dispatch({ type: 'SHOW_TOP_SEARCHBAR' })}
      />      
      <LocationGrid />
      <OurStory />      
      <SampleGrid />
      <TopHotelsGrid />
      <WhatWeDo />
      <LuxaryHotelsGrid />
      <Member />
      <Roadmap />
      <InfoRoadmap />
      <FAQ />      
    </>
  );
};

export default Home;
