import React from 'react';
import Container from 'components/UI/Container/Container';
import RoadmapWrapper from './InfoRoadmap.style';


const FAQ = () => {
  return (
    <RoadmapWrapper>
        <Container fluid={true}>
            <div class="timeline">
                <div class="container left">
                    <div class="date" style={{ right:"-120px" }}>Spring 2022</div>
                    <i class="icon fa fa-cog"></i>
                    <div class="content">
                    <h2>Business comes to the Blockchain</h2>
                    <p>
                        Spring of 2022 Joel decides to bring the business to the blockchain and starts putting the plan in motion.
                    </p>
                    </div>
                </div>
                <div class="container right">
                    <div class="date">June 2022</div>
                    <i class="icon fa fa-certificate"></i>
                    <div class="content">
                    <h2>Social media, marketing, and community creation</h2>
                    <p>                    
                    </p>
                    </div>
                </div>
                <div class="container left">
                    <div class="date">July 2022</div>
                    <i class="icon fa fa-home"></i>
                    <div class="content">
                    <h2>Social media following getting established</h2>
                    <p>
                        From May to early July 2022, the plan is to have a solid community and awareness of the project.
                    </p>
                    </div>
                </div>
                <div class="container right">
                    <div class="date">July 2022</div>
                    <i class="icon fa fa-gift"></i>
                    <div class="content">
                    <h2>VIP Whitelist exclusive mint</h2>
                    <p>
                        Early whitelist to be minted by early July
                    </p>
                    </div>
                </div>
                <div class="container left">
                    <div class="date" style={{ right:"-150px" }}>September 2022</div>
                    <i class="icon fa fa-user"></i>
                    <div class="content">
                    <h2>Public Launch</h2>
                    <p>
                        Public Launch by mid to late September 2022
                    </p>
                    </div>
                </div>
                <div class="container right">
                    <div class="date" style={{ left:"-140px" }}>October 2022</div>
                    <i class="icon fa fa-running"></i>
                    <div class="content">
                    <h2>Real Estate NFTs</h2>
                    <p>
                        Work on the Real Estate section of the protocol to begin after a successful initial launch.
                    </p>
                    </div>
                </div>
                <div class="container left">
                    <div class="date" style={{ right:"-140px" }}>November 2022</div>
                    <i class="icon fa fa-cog"></i>
                    <div class="content">
                    <h2>Planes and boats NFTs</h2>
                    <p>
                        After the Real Estate NFTs are launched
                    </p>
                    </div>
                </div>
                <div class="container right">
                    <div class="date" style={{ left:"-150px" }}>December 2022</div>
                    <i class="icon fa fa-certificate"></i>
                    <div class="content">
                    <h2>Plane Charter 135</h2>
                    <p>
                        We have a partnership with an Aviation school and we are in talks to bring private aviation.
                    </p>
                    </div>
                </div>
            </div>
        </Container>    
    </RoadmapWrapper>
  );
};

export default FAQ;
