import React from 'react';
import { Collapse, Col, Row } from 'antd';
import Container from 'components/UI/Container/Container';
import WhatWrapper from './WhatWeDo.style';

const WhatWeDo = () => {
  return (
    <WhatWrapper>      
      <Container fluid={true}>
        <Row gutter={30} style={{ marginTop: 30 }}>
            <Col xl={12} style={{ textAlign: "center"}}>
                <img src="images/home_section1.png"></img>
            </Col>
            <Col xl={10} className="textDiv">
                <h1>WHY WE DO WHAT WE DO?</h1>
                <p>
                  The	Premier	Protocol is one of the first protocols to give Fractional Ownership of Assets via NFTs.	
                  Imaging owning a piece of a Tesla that makes revenue and have someone else manage it. 
                  Own as much or as little as you'll like. It's revolutionary.
                </p>
            </Col>
        </Row>
      </Container>    
    </WhatWrapper>
  );
};

export default WhatWeDo;
