import styled from 'styled-components';
import { themeGet } from '@styled-system/theme-get';

const OurStoryWrapper = styled.div`  
  width: 100%;
  font-weight: bold;
  font-family: 'Berkshire Swash', sans-serif;

  h1 {
    font-family: 'Berkshire Swash', sans-serif;
    font-size: 50px;
    margin: 10px 0px;
    color: #008489;
  }
  h4 {
    font-family: 'Berkshire Swash', sans-serif;
    font-size: 20px;    
    color: #008489;
  }
  .stories {
    font-family: 'Berkshire Swash', sans-serif;
    font-size: 20px;
    text-align: left;
  }
  #story{
    font-family: 'Berkshire Swash', sans-serif;
    text-align: left;
  }
  .headBox{
    margin: auto;
    text-align: center;
    width: 60%;
    padding: 20px 0;
  }
  .box{
    margin: 20px auto;
    background: transparent;
  }
  @media screen and (max-width: 700px){
    .headBox{
      width: 90%;
    }
  }

  .read_more_button {
    margin-top: 10px;    
    border: 0;
    min-width: 115px;
    height: 40px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    font-size: 15px;
    font-weight: 700;
    border-radius: 3px;
    color: ${themeGet('color.1', '#ffffff')};
    border-color: ${themeGet('primary.0', '#008489')};
    background-color: ${themeGet('primary.0', '#008489')};
    transition: all 0.3s ease;
    &:hover {
      outline: none;
      opacity: 0.8;
    }
    &:focus {
      outline: none;
    }
    &::after {
      content: none;
    }    
  }
`;

export default OurStoryWrapper;