import React from 'react';
import Container from 'components/UI/Container/Container';
import RoadmapWrapper from './Roadmap.style';


const FAQ = () => {
  return (
    <RoadmapWrapper>
        <Container fluid={true}>
            <h1 class="title">Our Roadmap</h1>
            <div class="row main-body">
                <div class="col">
                <div class="main-timeline">
                    <div class="timeline">
                    <a href="#" class="timeline-content">
                        <span class="timeline-year">2019</span>
                        <div class="timeline-icon">
                        <i class="fa fa-rocket" aria-hidden="true"></i>
                        </div>
                        <div class="content">
                        <h3 class="title">PremierVacations,LLC is founded</h3>
                        <p class="description">
                            July 2019, Premier Vacations, LLC is founded by Joel Rodriguez and William Faulkner in the state of Florida.
                        </p>
                        </div>
                    </a>
                    </div>
                    <div class="timeline">
                    <a href="#" class="timeline-content">
                        <span class="timeline-year">2019</span>
                        <div class="timeline-icon">
                        <i class="fa fa-users" aria-hidden="true"></i>
                        </div>
                        <div class="content">
                        <h3 class="title">Vacation rental website</h3>
                        <p class="description">
                            Work on the vacation rental website gets started.<br></br>
                            <a href="https://www.mypremiervacationsllc.com" target="blank">www.mypremiervacationsllc.com</a>
                        </p>
                        </div>
                    </a>
                    </div>
                    <div class="timeline">
                    <a href="#" class="timeline-content">
                        <span class="timeline-year">2019</span>
                        <div class="timeline-icon">
                        <i class="fa fa-cog" aria-hidden="true"></i>
                        </div>
                        <div class="content">
                        <h3 class="title">Cars are listed for rent</h3>
                        <p class="description">
                            2017 Ford Fiesta and a 2008 Mini Cooper are the first cars to get listed for rent. <br></br>
                            <a href="https://turo.com/us/en/drivers/9170089" target="blank">View Cars</a>
                        </p>
                        </div>
                    </a>
                    </div>
                    <div class="timeline">
                    <a href="#" class="timeline-content">
                        <span class="timeline-year">2020</span>
                        <div class="timeline-icon">
                        <i class="fa fa-heart" aria-hidden="true"></i>
                        </div>
                        <div class="content">
                        <h3 class="title">Business is a growing business</h3>
                        <p class="description">
                            By the end of 2020 business is doing great and we continue to add properties and cars.
                        </p>
                        </div>
                    </a>
                    </div>
                </div>
                </div>
            </div>
        </Container>    
    </RoadmapWrapper>
  );
};

export default FAQ;
