import React, { useState } from 'react';
import Container from 'components/UI/Container/Container';
import { Button } from 'antd';
import OurStoryWrapper from './OurStory.style';


const FAQ = () => {
  
    const [readmoreStatus, setReadmoreStats] = useState(false)
    const handleReadMore = () => {
        let status = readmoreStatus;
        setReadmoreStats(!status);
    }
  
    return (
    <OurStoryWrapper id="about">
        <Container fluid={true}>
            <div className="headBox box">
                <h1>
                    Our Story
                </h1>
                <p className="stories">
                    Premier	is an actual company incorporated in the state of Florida.
                    The	companys name is Premier Vacations, LLC founded in	2019 between friends and business partners Joel Rodriguez and William Faulkner.
                    Joel has been in the Real Estate business since	2005 and William will do anything in business that makes sense and makes money.	
                    Joel and William went to the same high school in Haines City Florida and have been friends for a long time.
                </p>
                
                
                {readmoreStatus && 
                    <div>
                        <p className="stories">
                            Our	business	is	very	simple; we	rent	vacation	properties	and	also	rent	
                            cars.	We	are	in	the	business	of	having	fun and	creating	lasting	
                            memories.	Vacations	get everyone	in	a	good	mood	and	we	provide	great	
                            accommodations	at	fair	prices.	For	the	families	that	are	vacationing	in	
                            Orlando,	Florida,	we	can	also	provide	them	with	a	nice	rental	car	to	get	
                            around.	You	can	find	our	vacation	properties	at	<a href="https://www.mypremiervacationsllc.com" target="blank"> www.mypremiervacationsllc.com </a> and	you	can	find	our	rental	
                            cars	by	going to	<a href="https://turo.com/us/en/drivers/9170089" target="blank"> https://turo.com/us/en/drivers/9170089 </a> .	
                        </p>
                        <p className="stories">
                            Premier	is	different	from most	crypto	projects	because	we	have	
                            UTILITY.	We	believe	the	block	chain	and	defi	are	the	future,	but	not	the	
                            way	it is	mostly	done at	the	moment.	Most projects out there require new money coming in to pay the current holders.
                        </p>
                        <p className="stories">
                            NFTs	are	amazing	because	anybody	in	the	world	can	have	ownership	
                            and	it	can	be	verified	on	the	blockchain.	NFTs	give	access	to	projects	to	
                            anyone	with	none	of	the	usual	barriers	in	regular	finance.	For	the	
                            entrepreneur	it	is	also	great	because	they	can	access	investors	in	a	way	
                            that	was	not	possible	before.	Its	a	Win	Win.	
                        </p>

                        <h4>A	Glimmer	of	Hope	Emerges</h4>

                        <p className="stories">
                            Joel	loves	crypto	and	has	been	thinking	about	how	to	bring	the	business	
                            to	the	crypto	world.	He	has	found	the	way	via	NFTs.	Basically	we	have	
                            decided	to	take	our	assets;	we	are	starting	with	the	cars,	and	split	
                            them	in	to	Fractions	(Fractional	Ownership).	Each	NFT	represents	a	
                            fraction	of	an	actual	asset	(ie.cars,	real	estate,	planes,	and	boats).	The	
                            NFT	then	earns	a	share	of	the	income	produced	by	the	asset	paid	to	the	
                            users	wallet.	
                        </p>
                    </div>
                }

                <Button className="read_more_button" onClick={handleReadMore}>
                    {!readmoreStatus && 'Read More'}
                    {readmoreStatus && 'Read Less'}
                </Button>
            </div>            
        </Container>    
    </OurStoryWrapper>
  );
};

export default FAQ;
